import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			initData: {},
			expressData: {}
		};
	},
	methods: {
		linkToDelivery(orderId){
			uni.navigateTo({
				url: '/pages/delivery/delivery?orderId=' + orderId
			})
		},
		linkToPayment(order){




			this.$http.post('/mall/app/order/submit', {
				...this.baseInfo,
				accountId: order.accountId,
				addressId: order.addressId,
				credits: order.credits,
				expressFee: order.expressPay,
				goodsList: order.details,
				payPrice: order.payPrice,
				remark: order.orderlistMessage,
				// totalPrice: this.payData.totalPrice,
				// goodsPrice: this.totalPriceDecrease,
				// address: this.address,
				payChannels: '8',
				callBackNo: order.orderlistNum
			})
			.then( res => {
				if(res.code == 0){
					wx.requestPayment({
						'timeStamp': res.result.payResponse.wxPayResponse.timeStamp,
						'nonceStr': res.result.payResponse.wxPayResponse.nonceStr,
						'package': res.result.payResponse.wxPayResponse.packageStr,
						'signType': res.result.payResponse.wxPayResponse.signType,
						'paySign': res.result.payResponse.wxPayResponse.paySign,
						'success': function (sc) {
							uni.redirectTo({
								url: '/pages/orderDetail/orderDetail?orderId='+resultOrderId
							});
						},
						'fail': function (er) {
							uni.redirectTo({
								url: '/pages/order/order?state=0'
							})
						},
						'complete': function(msg){
							console.log(msg);
						}
					})
				}else{
					uni.showToast({
						icon: 'none',
						title: res.message
					})
				}
			})
			.catch( err => {
				console.log(err);
			})

		},
		confirmOrder(orderId){
			this.$http.post('/mall/app/order/receive', {
				...this.baseInfo,
				accountId: this.userInfo.accountId,
				orderId: orderId
			})
			.then( res => {
				if(res.code == 0){
					uni.showToast({
						icon: 'none',
						title: '确认收货成功'
					})
					this.loadData(orderId);
				}
			})
			.catch( err => {
				console.log(err);
			})
		},
		cancelOrder(orderId){
			uni.showModal({
			    title: '提示',
			    content: '确定取消订单吗？',
				confirmColor: '#E93B3D',
			    success: res => {
			        if (res.confirm) {
			            this.$http.post('/mall/app/order/cancel', {
							...this.baseInfo,
							accountId: this.userInfo.accountId,
							orderId: orderId
						})
						.then( res => {
							if(res.code == 0){
								uni.showToast({
									icon: 'none',
									title: '取消订单成功'
								})
								this.loadData(orderId);
								setTimeout(function(){
									uni.redirectTo({
									    url: '/pages/order/order?state=4'
									});
								}, 2000);
							}else{
								uni.showToast({
									icon: 'none',
									title: '取消订单失败'
								})
								this.loadData();
							}
						})
						.catch( err => {
							console.log(err);
						})
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		makePhoneCall(){
			uni.makePhoneCall({
			    phoneNumber: '4009006669'
			});
		},
		transformStatus(num){
			switch(num){
				case -1:
					return '待查询';
					break;
				case 0:
					return '查询异常';
					break;
				case 1:
					return '暂无记录';
					break;
				case 2:
					return '在途中';
					break;
				case 3:
					return '派送中';
					break;
				case 4:
					return '已签收';
					break;
				case 5:
					return '用户拒签';
					break;
				case 6:
					return '疑难件';
					break;
				case 7:
					return '无效单';
					break;
				default:
					return '';
			}
		},
		initExpress(orderId){
			this.$http.post('/mall/app/order/express', {
				...this.baseInfo,
				accountId: this.userInfo.accountId,
				orderId: orderId
			})
			.then( res => {
				if(res.code == 0){
					this.expressData=JSON.parse(res.result.express).showapi_res_body;
					this.expressData.statusName=this.transformStatus(this.expressData.status);
					this.expressData.data=this.expressData.data[0];
				}
			})
			.catch( err => {
				console.log(err);
			})
		},
		// transformTime(time){
		// 	let moment = require('moment');
		// 	let day = moment(time, moment.ISO_8601);
		// 	return day.format("YYYY-MM-DD  h:mm:ss") || '';
		// },
		loadData(orderId){
			this.$http.post('/mall/app/order/detail', {
				...this.baseInfo,
				accountId: this.userInfo.accountId,
				orderId: orderId
			})
			.then( res => {
				if(res.code == 0){
					this.initData=res.result;
					// this.initData.transOrderlistTime=this.transformTime(res.result.orderlistTime);
					// this.initData.transPayTime=this.transformTime(res.result.payTime);
				}
			})
			.catch( err => {
				console.log(err);
			})
		}
	},
	computed: {
		...mapState('common', ['baseInfo', 'userInfo','baseUrl', 'paymentUrl', 'token'])
	},
	onLoad(options){
		this.loadData(options.orderId);
		this.initExpress(options.orderId);
	}
}
